/* General styles */
body {
    background-color: var(--background-color);
    color: var(--text-color);
    font-family: 'Helvetica', 'Arial', sans-serif;
    margin: 0;
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

/* Light and Dark Theme Variables */
:root {
    --background-color: black;
    --text-color: white;
    --primary-color: #FF5722;
    --section-background-color: #1C1C1C; /* Dark section background */
}

.light-theme {
    --background-color: white;
    --text-color: black;
    --primary-color: #FF5722;
    --section-background-color: #f2f2f2; /* Light grey section background for light mode */
}

/* Theme Toggle in Navbar */
.theme-toggle-container {
    display: flex;
    align-items: center;
    padding: 0px 0;
    font-size: 1rem; /* Ensure same font size as navbar items */
}

.theme-toggle-label {
    color: var(--text-color);
    margin-right: 10px;
}

.theme-toggle {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 25px;
}

.theme-toggle input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: 0.4s;
    border-radius: 25px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 21px;
    width: 21px;
    left: 2px;
    bottom: 2px;
    background-color: white;
    transition: 0.4s;
    border-radius: 50%;
}

input:checked + .slider {
    background-color: var(--primary-color);
}

input:checked + .slider:before {
    transform: translateX(25px);
}

/* Page Container */
.page-container {
    padding: 20px;
    background-color: var(--section-background-color); /* Adapt section background based on theme */
}

.top-section {
    text-align: center;
    margin-bottom: 20px;
}

.title {
    font-size: 3rem;
    color: var(--text-color);
    margin: 0;
}

.description {
    color: var(--text-color); /* Ensure description adapts based on theme */
    font-size: 1.2rem;
}

/* Content Sections */
.content-container {
    display: flex;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;
}

.left-column,
.right-column {
    width: 48%;
    background-color: var(--section-background-color); /* Use light grey for light mode */
    padding: 20px;
    border-radius: 10px;
}

h2 {
    color: var(--text-color);
}

/* Add Ingredient Form */
.add-ingredient-form .ingredient-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    color: var(--text-color);
    padding: 10px 15px;
    border-radius: 5px;
    margin-bottom: 10px;
}

.add-ingredient-form input {
    padding: 10px;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    background-color: white;
    width: 85%;
}

.add-btn {
    background-color: #696969;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.add-btn:hover {
    background-color: #888;
}

/* Ingredient list */
.ingredient-box {
    background-color: #333;
    color: var(--text-color);
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    transition: background-color 0.3s ease-in-out;
}

.ingredient-box.selected {
    background-color: #696969;
}

.actions {
    display: flex;
}

/* Delete Button Icon */
.delete-btn {
    background-color: transparent;
    border: none;
    font-size: 24px;
    color: red; /* Red trash icon */
    cursor: pointer;
}

.delete-btn:hover {
    color: darkred;
}

/* Recipe card styles */
.recipe-item {
    background-color: #333;
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 15px;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: transform 0.2s ease-in-out;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);
}

.recipe-item:hover {
    transform: scale(1.05);
}

.recipe-title {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 5px;
}

.recipe-time {
    color: #FF5722;
    margin-bottom: 10px;
}

/* Recipe Preview Card */
.recipe-card img {
    max-width: 100%;
    border-radius: 10px;
    margin-bottom: 10px;
}

/* Favorites Star Icon */
.star-btn {
    background-color: transparent;
    border: none;
    font-size: 24px;
    color: white;
    cursor: pointer;
}

.star-btn.active {
    color: gold;
}

.star-btn:hover {
    color: yellow;
}

/* Find Recipes Button */
.find-recipes-btn {
    background-color: var(--primary-color);
    color: white;
    padding: 15px;
    font-size: 1.2rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    width: 100%;
    margin-top: 20px;
    text-align: center;
}

.find-recipes-btn:hover {
    background-color: #E64A19;
}

/* Scrollable ingredient list */
.select-ingredients-container {
    max-height: 250px;
    overflow-y: auto;
    margin-bottom: 20px;
}

/* Scrollable Recipe list */
.right-column .recipe-list {
    max-height: 420px;
    overflow-y: auto;
    border-radius: 5px;
    border: 1px solid #444;
    padding: 15px;
    margin-bottom: 20px;
}

/* Cuisine filter dropdown */
.cuisine-select {
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    background-color: white;
    border: 1px solid #ccc;
    color: black; /* Ensure dropdown text adapts based on theme */
    margin-bottom: 20px;
    font-size: 1rem;
    transition: background-color 0.3s ease-in-out;
}

.cuisine-select:focus {
    border-color: var(--primary-color);
}

/* Placeholder & Loader */
.recipe-placeholder,
.loader {
    text-align: center;
    font-size: 1.2rem;
    color: #aaa;
    padding: 20px;
    border: 2px dashed #555;
    border-radius: 5px;
}

.loader {
    border: 8px solid #f3f3f3;
    border-top: 8px solid #3498db;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: spin 2s linear infinite;
    margin: 20px auto;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

/* Profile Icon */
.profile-container {
    position: absolute;
    top: 10px;
    right: 10px;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.profile-icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: var(--primary-color);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    color: white;
    font-weight: bold;
}

/* Navbar styles */
.vertical-navbar {
    position: fixed;
    left: -250px;
    top: 0;
    height: 100%;
    width: 250px;
    background-color: #333;
    transition: 0.3s ease-in-out;
    padding-top: 60px;
    z-index: 1000;
}

.vertical-navbar.open {
    left: 0;
}

.vertical-navbar ul {
    list-style-type: none;
    padding: 0;
}

.vertical-navbar ul li {
    padding: 10px 15px;
    text-align: left;
    color: var(--text-color);
}

.vertical-navbar ul li a {
    color: var(--text-color);
    text-decoration: none;
    display: block;
}

.vertical-navbar ul li a:hover {
    background-color: #575757;
}

.navbar-toggle-btn {
    position: fixed;
    top: 15px;
    left: 15px;
    font-size: 30px;
    background-color: transparent;
    border: none;
    color: var(--text-color);
    cursor: pointer;
}

.navbar-back-arrow {
    position: absolute;
    top: 10px;
    right: 15px;
    font-size: 25px;
    background-color: transparent;
    border: none;
    color: var(--text-color);
    cursor: pointer;
}

/* Favorites page content */
.favorites-page {
    margin-top: 60px;
    padding: 20px;
}

.favorites-page {
    padding-top: 80px;
}
