.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.login-form {
    background-color: #333;
    padding: 20px;
    border-radius: 10px;
    width: 300px;
    text-align: center;
}

    .login-form input {
        display: block;
        width: 100%;
        padding: 10px;
        margin-bottom: 10px;
        border: none;
        border-radius: 5px;
        background-color: #444;
        color: white;
    }

    .login-form button {
        background-color: #FF5722;
        color: white;
        padding: 10px;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        width: 100%;
    }

.error-message {
    color: red;
    margin-top: 10px;
}

.success-message {
    color: green;
    margin-top: 10px;
}

.signup-link, .login-link {
    margin-top: 10px;
    color: white;
}

    .signup-link a, .login-link a {
        color: orange;
        text-decoration: none;
    }

        .signup-link a:hover, .login-link a:hover {
            text-decoration: underline;
        }
